@import "../utils/variables";

.header {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
  padding: 0 50px;

  background: $background-color;
  box-shadow: 0 2px 4px $header-box-shadow;

  &__link {
    &--logo {
      font-size: 0;
    }
  }

  &__logo {
    width: 40px;
    height: 40px;
    margin-right: 50px;
  }
}

.nav {
  &__item {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 20px;

    text-align: center;
  }

  &__item:last-child {
    margin-right: 0;
  }

  &__list {
    display: flex;
  }

  &__link {
    text-transform: uppercase;
    text-decoration: none;
    text-align: center;

    padding: 23px 0;

    &:hover {
      color: $blue-accent;
    }

    &--active {
      position: relative;
      color: $blue-accent;
    }

    &--active::after {
      display: block;
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 4px;
      background-color: $blue-accent;
      border-radius: 8px;

      content: "";
    }
  }
}
